import { Modal } from 'antd';
import './index.scss'

export default (props) => {
    let { visible, list, setVisible } = props
    let closeIcon = <span className="ant-icon-close"></span>
    let closeModal = () => setVisible(false)
    return (
        <Modal title="" closeIcon={closeIcon} open={visible} okText="" onOk={closeModal} cancelText="" onCancel={closeModal}>
            <div className="ant-confirm-body">
                <i className="anticon anticon-close-circle" style={{ color: '#f00' }}></i>
                <span className="ant-confirm-title"></span>
                <div className="ant-confirm-content">
                    <div>
                        <p className="error-modal-desc"></p>
                        <div className="error-modal-list">
                            {
                                list.map((item, index) => {
                                    return (
                                        <div className="error-modal-item" key={index}>
                                            <div className="error-modal-item-label"></div>
                                            <div className="error-modal-item-content">{item.name} {item.type}</div>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                </div>
            </div>
        </Modal>
    )
}